@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap);
:root {
  /* COLORS */
  --primary:#FEEBD9;
  --secondary:#F57C00;
  --third:#EEEEEE;
  --info:#0dcaf0;

  /* FONT FAMMILY */
  --mainFont:'Montserrat', sans-serif;

  /* FONT SIZE */
  --normalSize: 20px;

  /* INPUT SELECT */
  --input-title:18px;
  --input-content:16px;
}

*{
  font-family: 'Montserrat', sans-serif;
  font-family: var(--mainFont);
}

body{
  font-size:16px;
  background-color: #EEEEEE;
  background-color: var(--third);
}

.navbar-logo{
    width: 120px;
}

.navbar-menu{
    border: none;
    background: transparent;
}

.navbar-menu:focus{
    box-shadow: none;
}

/* Elementos del menu */

.nav-item{
    font-size:var(--normalSize);
}

.nav-item:hover{
    background-color: var(--primary);
}

.nav-item:hover  .nav-link-text{
    color: var(--secondary) !important;
}

.nav-item:hover  .nav-icon{
    color: var(--secondary) !important;
}

/* Enlaces del menu */

.nav-link:hover{
    cursor: pointer !important;
}

.nav-link-text{
    letter-spacing: 1px !important;
    margin: 0px;
    font-weight: 500;
}

.nav-icon{
    width: 35px;
    color:black;
    height: 100%;
}

/* Dropdowm */
.dropdown-menu{
    border: 0px !important;
    background-color: var(--primary);
}

.dropdown-item{
    /* letter-spacing: 2px !important; */
    font-weight: 500;
}

.dropdown-item:hover{
    background-color: white !important;
}

.dropdown-link{
    text-decoration: none;
}

@media(min-width: 992px){
    .dropdown-menu{
        margin-left:calc(100% - 237px);
    }
}


    
.card-form{  
    background-color:white;
    box-shadow: 2px 2px 0px 0px #ccc;
}

@media(max-width:991px){
    .card-form{
        width:90%
    }
}
.inputSelect{
    outline: none !important;
    border: none;
    font-size: var(--input-content);
}

.input-title {
    font-size:var(--IS-size-title);
  }
.input-date{
    padding-left: 5px;
    outline:none;
    font-size: var(--input-content);
}
.input-file-title{
    font-size: 20px;
    font-weight: 500;
}

.input-file{
    color: var(--info);
    font-weight: bold;
    margin-left: 10px;
}

.input-file::-webkit-file-upload-button{
    background-color: var(--secondary);
    color: white;
    outline: none;
    border: none;
    padding: 6px 10px;
    border-radius:5px;
    margin-right: 10px;
    font-weight: normal;
}

.input-file::-ms-browse{
    background-color: var(--secondary);
}
.inputText {
  border: none;
  border-bottom: 2px solid var(--secondary);
  width: 95%;
  padding-bottom: 5px;
  outline: none;
  color: var(--secondary);
}

.inputText::-webkit-input-placeholder {
  color: var(--secondary);
  font-size: 16px;
}

.inputText:-ms-input-placeholder {
  color: var(--secondary);
  font-size: 16px;
}

.inputText::placeholder {
  color: var(--secondary);
  font-size: 16px;
}

.input-icon {
  font-size: 15px;
  cursor: pointer;
  transition: all 0.5s;
  position: relative;
}

.input-icon:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.input-icon-content:hover .input-info {
  display: block;
}

.input-error{
  color:red;
  font-size: 16px;
  margin-top: 5px;
}

:root{
    --input-info-border:2px;
}

.input-info{
    display: none;
    position: absolute;
    width: 350px;
    margin-left: -370px;
    background-color: var(--primary); 
    font-size: 14px;
    padding: 10px;
    border: 2px solid var(--secondary);
    border: var(--input-info-border) solid var(--secondary);
}

.input-info::after{
    content:'';
    width: 10px;
    height: 20px;
    position:absolute;
    left:calc(100% + 2px);
    left:calc(100% + var(--input-info-border));
    top: calc(0% - 2px);
    top: calc(0% - var(--input-info-border));
    border-top:10px solid transparent !important;
    border-bottom: 10px solid transparent !important;
    border-left: 10px solid var(--secondary) !important;
    border-right: 10px solid transparent !important;
    
}

.input-info-link{
    color: var(--secondary);
    font-weight:600;
}
